export default {
	data() {
		return {
			CurrentPage: 'user',
		}
	},
	onLoad() {

	},
	methods: {
		Toggle(e) {
			var anmiaton = e.currentTarget.dataset.class;
			this.animation = anmiaton;
			setTimeout(() => {
				this.animation = '';
			}, 1000)
		},
		NavChange(e) {
			this.CurrentPage = e.page;
		}
	}
}
